@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-white dark:bg-gray-900 text-gray-900 dark:text-gray-100 transition-colors duration-300;
  }
}

@layer components {
  .btn {
    @apply inline-flex items-center gap-2 px-6 py-3 rounded-lg font-medium no-underline transition-all duration-300 cursor-pointer border-none text-base;
  }
  
  .btn-primary {
    @apply bg-primary-400 text-white hover:bg-primary-500 hover:-translate-y-0.5 hover:shadow-lg;
  }
  
  .btn-secondary {
    @apply bg-transparent text-gray-900 dark:text-gray-100 border border-gray-300 dark:border-gray-600 hover:bg-gray-100 dark:hover:bg-gray-800 hover:border-primary-400;
  }
  
  .card {
    @apply bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-xl p-6 shadow-md hover:-translate-y-1 hover:shadow-lg transition-all duration-300 cursor-pointer;
  }
  
  .section-title {
    @apply text-4xl md:text-5xl font-bold mb-8 bg-gradient-to-r from-primary-400 to-primary-500 bg-clip-text text-transparent;
  }
  
  .section-subtitle {
    @apply text-xl text-gray-600 dark:text-gray-400 mb-12 max-w-2xl;
  }
} 